'use strict';

import { closeModal, openModal } from './scriptModal';
import { postData } from '../services/services';

function formData(formSelector) {
  const forms = document.querySelectorAll(formSelector);

  //       
  const message = {
    loading: '../../icons/spinner.svg',
    success: '!     ',
    failure: '-   ...',
  };

  forms.forEach((form) => bindPostData(form));

  //   
  function bindPostData(form) {
    form.addEventListener('submit', (e) => {
      e.preventDefault(); //   ( )

      const messageElement = document.createElement('img');
      messageElement.src = message.loading;
      messageElement.style.cssText = `
            display: block;
            margin: 0 auto;
        `;

      form.insertAdjacentElement('afterend', messageElement);

      // const request = new XMLHttpRequest();   
      // request.open('POST', 'server.php');

      //request.setRequestHeader('Content-type', 'application/json');

      //XMLHttpRequest + FormData -   
      // 

      // const object = {};
      // formData.forEach((value, key) => {
      //     object[key] = value;
      // }); //   formData  (      json)

      //request.send(formData); //    formData
      // request.send(JSON.stringify(object));//    JSON

      // request.addEventListener('load', () => {
      //     if (request.status === 200) {
      //         console.log(request.response);
      //         showThanksModal(message.success);
      //         form.reset(); // 
      //         statusMessage.remove();// 
      //     } else {
      //         showThanksModal(message.failure);
      //     }
      // });

      // fetch('server.php', { //      formData
      //     method: 'POST',
      //     body: formData
      // }).then(response => response.text())
      // .then(data => {  //  
      //     console.log(data);
      //     showThanksModal(message.success);
      // }).catch(() => {
      //     showThanksModal(message.failure);
      // }).finally(() => form.reset()); // 

      // ,        
      //:   formData    input   name,      input
      const formData = new FormData(form);

      //  formData     ,    ,    json
      const json = JSON.stringify(Object.fromEntries(formData.entries()));

      postData('http://localhost:3000/requests', json)
        .then((data) => {
          console.log(data);
          showThanksModal(message.success);
          messageElement.remove();
        })
        .catch(() => showThanksModal(message.failure))
        .finally(() => form.reset());
    });
  }

  function showThanksModal(message) {
    const prevModalDialog = document.querySelector('.modal__dialog'),
      modal = document.querySelector('.modal');

    prevModalDialog.classList.add('hide');
    openModal('.modal');

    const newModalDialog = document.createElement('div');
    newModalDialog.classList.add('modal__dialog');

    newModalDialog.innerHTML = `
        <div class="modal__content">
            <div data-close class="modal__close">&times;</div>
            <div class="modal__title">${message}</div>
        </div>
    `;
    modal.append(newModalDialog);

    setTimeout(() => {
      newModalDialog.remove();
      prevModalDialog.classList.remove('hide');
      prevModalDialog.classList.add('show');
      closeModal('.modal');
    }, 4000);
  }
}

export default formData;
